import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import './App.css';
import * as Pages from './Pages.js';
import Nav from "./Components/Nav"



function App() {
  return (
    <div className="App">
      <Router>
        <Nav></Nav>
        <Route path="/" exact component={Pages.Home}></Route>
        <Route path="/abstracts" exact component={Pages.Abstracts}></Route>
        <Route path="/trials" exact component={Pages.ClinicalTrials}></Route>

       </Router>
    </div>
  );
}

export default App;
